.card-emprestimo{width:100%;height:7rem;position:relative;cursor:pointer;border-radius:.5rem}.card-emprestimo:last-child{margin:0}.card-emprestimo .cover{width:100%;height:100%;position:absolute;background:linear-gradient(45deg, rgba(0, 0, 0, 0.616), rgba(0, 0, 0, 0));z-index:2;border-radius:.5rem}.card-emprestimo img[alt="PENSIONISTAS INSS"]{object-position:left 30%}.card-emprestimo img[alt="SERVIDORES PÚBLICOS"]{object-position:left 30%}.card-emprestimo img[alt="FORÇAS ARMADAS"]{object-position:center left}.card-emprestimo img[alt="CARTÃO DE CRÉDITO CONSIGNADO"]{object-position:center left}.card-emprestimo img[alt="PORTABILIDADE DE CRÉDITO"]{object-position:center left}.card-emprestimo img{width:100%;height:100%;position:absolute;object-fit:cover;object-position:top left;z-index:1;border-radius:.5rem}.card-emprestimo h3{display:flex;justify-content:flex-end;position:absolute;padding:1rem;bottom:0;left:0;color:#fff;z-index:3}